import './entry-form.scss';
import Confetti from 'react-confetti';
import { useState } from 'react';

export default function EntryForm(): JSX.Element {
  const [confetti, setConfetti] = useState(false);

  return (
    <form className="entry-form">
      <h2> : </h2>
      {
        confetti ?
          <div className='confetty-wrap'>
            <Confetti width={1920} recycle={false}/>
          </div> :
          null
      }

      <div className='container'>
        <div className="entry-form__title">
        </div>
        <fieldset>
          <label htmlFor="">
         
            <select name="" id="">
              <option value=""></option>
            </select>
          </label>
        </fieldset>
        <fieldset>
          <label htmlFor="">
           ():
            <input type="text" />
          </label>
          <label htmlFor="">
          :
            <input type="text" />
          </label>
          <label htmlFor="">
         2:
            <input type="text" />
          </label>
          <label htmlFor="">
          :
            <input type="text" />
          </label>
        </fieldset>

        <fieldset>
          <label htmlFor="">
          :
            <input type="text" />
          </label>
          <label htmlFor="">
          :
            <input type="text" />
          </label>
          <label htmlFor="">
        :
            <input type="text" />
          </label>
          <label htmlFor="">
          :
            <input type="text" />
          </label>
        </fieldset>

        <fieldset>
          <label htmlFor="">
          :
            <input type="text" />
          </label>
          <label htmlFor="">
          :
            <input type="text" />
          </label>
          <label htmlFor="">
         :
            <input type="text" />
          </label>
        </fieldset>

        <fieldset>
          <label htmlFor="">
          :
            <textarea name="" id="" cols={30} rows={10}></textarea>
          </label>
          <label htmlFor="">
           :
            <input type="date" name="" id="" />
          </label>
        </fieldset>
        <div className='offer-result-buttons'>
          <div className='offer-btn arrange-btn' title='  ' onClick={() => setConfetti(true)}></div>
        </div>
      </div>

    </form>
  );
}
